/* ---------------------
  Header
-----------------------  */
.header {
  background: $white-color;
  box-shadow: 0 5px 10px rgba(91, 91, 91, 0.1);
}

.header__logo {
  padding: 26px 0;

  a {
    display: inline-block;
  }
}

.header__menu {
  padding: 30px 0 27px;

  ul {
    li {
      list-style: none;
      display: inline-block;
      margin-right: 40px;
      position: relative;

      &.active {
        a {
          &::after {
            -webkit-transform: scale(1);
            transform: scale(1);
          }
        }
      }

      &:hover {
        a {
          &::after {
            -webkit-transform: scale(1);
            transform: scale(1);
          }
        }

        .dropdown {
          top: 27px;
          opacity: 1;
          visibility: visible;
        }
      }

      &:last-child {
        margin-right: 0;
      }

      .dropdown {
        position: absolute;
        left: 0;
        top: 56px;
        width: 150px;
        background: $heading-color;
        text-align: left;
        padding: 2px 0;
        z-index: 9;
        opacity: 0;
        visibility: hidden;

        @include transition(all, 0.3s);

        li {
          display: block;
          margin-right: 0;

          a {
            font-size: 14px;
            color: $white-color;
            font-weight: 400;
            padding: 8px 20px;
            text-transform: capitalize;

            &::after {
              display: none;
            }
          }
        }
      }

      a {
        font-size: 15px;
        text-transform: uppercase;
        color: $heading-color;
        font-weight: 500;
        display: block;
        padding: 2px 0;
        position: relative;

        &::after {
          position: absolute;
          left: 0;
          bottom: 0;
          width: 100%;
          height: 2px;
          background: $primary-color;
          content: '';

          @include transition(all, 0.5s);

          -webkit-transform: scale(0);
          transform: scale(0);
        }
      }
    }
  }
}

.header__right {
  text-align: right;
  padding: 30px 0 27px;
}

.header__right__auth {
  display: inline-block;
  margin-right: 25px;

  a {
    font-size: 12px;
    color: #666;
    position: relative;
    margin-right: 8px;

    &:last-child {
      margin-right: 0;

      &::after {
        display: none;
      }
    }

    &::after {
      position: absolute;
      right: -8px;
      top: -2px;
      content: "/";
      font-size: 13px;
    }
  }
}

.header__right__widget {
  display: inline-block;

  li {
    list-style: none;
    display: inline-block;
    font-size: 18px;
    color: $heading-color;
    margin-right: 20px;
    cursor: pointer;

    &:last-child {
      margin-right: 0;
    }

    a {
      font-size: 18px;
      color: $heading-color;
      position: relative;

      .tip {
        position: absolute;
        right: -12px;
        top: -11px;
        height: 18px;
        width: 18px;
        background: $heading-color;
        font-size: 10px;
        font-weight: 500;
        color: $white-color;
        line-height: 18px;
        text-align: center;
        border-radius: 50%;
      }
    }
  }
}

.offcanvas-menu-wrapper {
  display: none;
}

.canvas__open {
  display: none;
}



