/* ---------------------
  Shop Cart
----------------------- */
.shop-cart {
  padding-top: 70px;
  padding-bottom: 90px;
}

.shop__cart__table {
  margin-bottom: 30px;

  table {
    width: 100%;
  }

  thead {
    border-bottom: 1px solid #f2f2f2;

    th {
      font-size: 18px;
      color: $heading-color;
      font-weight: 600;
      text-transform: uppercase;
      padding-bottom: 20px;
    }
  }

  tbody {
    tr {
      border-bottom: 1px solid #f2f2f2;

      td {
        padding: 30px 0;
      }

      .cart__product__item {
        overflow: hidden;
        width: 585px;

        img {
          float: left;
          margin-right: 25px;
        }

        .cart__product__item__title {
          overflow: hidden;
          padding-top: 23px;

          h6 {
            color: $heading-color;
            font-weight: 600;
          }

          .rating {
            i {
              font-size: 10px;
              color: #e3c01c;
              margin-right: -4px;
            }
          }
        }
      }

      .cart__price {
        font-size: 16px;
        color: $primary-color;
        font-weight: 600;
        width: 190px;
      }

      .cart__quantity {
        width: 190px;

        .pro-qty {
          border: none;
          padding: 0;
          width: 110px;
          border-radius: 0;

          input {
            color: #444;
          }

          .qtybtn {
            font-size: 16px;
            color: #444;
          }
        }
      }

      .cart__total {
        font-size: 16px;
        color: $primary-color;
        font-weight: 600;
        width: 150px;
      }

      .cart__close {
        text-align: right;

        span {
          height: 45px;
          width: 45px;
          background: #f2f2f2;
          border-radius: 50%;
          font-size: 18px;
          color: $heading-color;
          line-height: 44px;
          text-align: center;
          display: inline-block;
          font-weight: 600;
          cursor: pointer;
        }
      }
    }
  }
}

.cart__btn {
  margin-bottom: 50px;

  &.update__btn {
    text-align: right;
  }

  a {
    font-size: 14px;
    color: $heading-color;
    font-weight: 600;
    text-transform: uppercase;
    display: inline-block;
    padding: 14px 30px 12px;
    background: #f5f5f5;

    span {
      color: $primary-color;
      font-size: 14px;
      margin-right: 5px;
    }
  }
}

.discount__content {
  h6 {
    color: $heading-color;
    font-weight: 600;
    text-transform: uppercase;
    display: inline-block;
    margin-right: 30px;
  }

  form {
    position: relative;
    width: 370px;
    display: inline-block;

    input {
      height: 52px;
      width: 100%;
      border: 1px solid #444;
      border-radius: 50px;
      padding-left: 30px;
      padding-right: 115px;
      font-size: 14px;
      color: #444;

      &::placeholder {
        color: #444;
      }
    }

    button {
      position: absolute;
      right: 4px;
      top: 4px;
    }
  }
}

.cart__total__procced {
  background: #f5f5f5;
  padding: 40px;

  h6 {
    color: $heading-color;
    font-weight: 600;
    text-transform: uppercase;
    margin-bottom: 10px;
  }

  ul {
    margin-bottom: 25px;

    li {
      list-style: none;
      font-size: 16px;
      color: $heading-color;
      font-weight: 600;
      overflow: hidden;
      line-height: 40px;

      span {
        color: $primary-color;
        float: right;
      }
    }
  }

  .primary-btn {
    display: block;
    border-radius: 50px;
    text-align: center;
    padding: 12px 0 10px;
  }
}



