/* ---------------------
  Blog Details
----------------------- */
.blog-details {
  padding-top: 80px;
  padding-bottom: 80px;
}

.blog__details__item {
  background: $white-color;
  margin-bottom: 24px;
}

.blog__details__item__title {
  background: $white-color;
  padding-top: 30px;
  padding-right: 30px;
  margin-top: -100px;
  position: relative;
  margin-right: 50px;

  .tip {
    font-size: 12px;
    color: $white-color;
    background: $primary-color;
    text-transform: uppercase;
    font-weight: 500;
    display: inline-block;
    padding: 2px 11px 1px;
    border-radius: 5px;
  }

  h4 {
    color: $heading-color;
    font-weight: 600;
    line-height: 39px;
    margin-top: 10px;
    margin-bottom: 5px;
  }

  ul {
    li {
      font-size: 12px;
      color: #888;
      display: inline-block;
      list-style: none;
      margin-right: 25px;
      position: relative;

      span {
        color: $heading-color;
      }

      &::after {
        position: absolute;
        right: -17px;
        top: 0;
        content: "|";
      }

      &:last-child {
        margin-right: 0;

        &::after {
          display: none;
        }
      }
    }
  }
}

.blog__details__desc {
  margin-bottom: 40px;

  p {
    font-size: 15px;
    color: #444;
    line-height: 27px;

    &:last-child {
      margin-bottom: 0;
    }
  }
}

.blog__details__quote {
  border-top: 2px solid $primary-color;
  position: relative;
  padding-top: 22px;
  margin-bottom: 20px;

  .icon {
    height: 30px;
    width: 30px;
    font-size: 18px;
    color: $primary-color;
    background: $white-color;
    position: absolute;
    left: 0;
    top: -11px;
  }

  p {
    font-size: 16px;
    color: $heading-color;
    font-weight: 600;
    font-style: italic;
    line-height: 30px;
    margin-bottom: 0;
  }
}

.blog__details__tags {
  margin-bottom: 50px;

  a {
    display: inline-block;
    font-size: 13px;
    color: #666;
    border: 1px solid #f2f2f2;
    padding: 8px 14px 7px;
    margin-right: 6px;
    margin-bottom: 10px;

    @include transition(all, 0.3s);

    &:hover {
      color: $heading-color;
    }

    &:last-child {
      margin-right: 0;
    }
  }
}

.blog__details__btns {
  background: #f5f5f5;
  padding: 14px 30px;
  margin-bottom: 55px;
}

.blog__details__btn__item {
  &.blog__details__btn__item--next {
    text-align: right;

    h6 {
      a {
        i {
          margin-right: 0;
          margin-left: 5px;
        }
      }
    }
  }

  h6 {
    a {
      font-size: 15px;
      font-weight: 600;
      text-transform: uppercase;
      color: $heading-color;

      i {
        font-size: 15px;
        font-weight: 600;
        position: relative;
        top: -2px;
        margin-right: 5px;
      }
    }
  }
}

.blog__details__comment {
  position: relative;

  h5 {
    color: $heading-color;
    font-weight: 600;
    text-transform: uppercase;
    margin-bottom: 35px;
  }

  .leave-btn {
    font-size: 14px;
    font-weight: 700;
    color: $heading-color;
    text-transform: uppercase;
    position: absolute;
    right: 0;
    top: 0;

    &::after {
      position: absolute;
      left: 0;
      bottom: -5px;
      height: 1px;
      width: 100%;
      background: $primary-color;
      content: "";
    }
  }
}

.blog__comment__item {
  margin-bottom: 35px;

  &:last-child {
    margin-bottom: 0;
  }

  &.blog__comment__item--reply {
    padding-left: 115px;
  }
}

.blog__comment__item__pic {
  float: left;
  margin-right: 25px;

  img {
    border-radius: 50%;
  }
}

.blog__comment__item__text {
  overflow: hidden;

  h6 {
    color: $heading-color;
    font-weight: 600;
    margin-bottom: 14px;
  }

  p {
    font-size: 15px;
    color: #444;
    line-height: 26px;
  }

  ul {
    li {
      list-style: none;
      font-size: 12px;
      color: #888;
      display: inline-block;
      margin-right: 25px;

      &:last-child {
        margin-right: 0;
      }

      i {
        font-size: 14px;
        color: $primary-color;
        margin-right: 5px;
      }
    }
  }
}



